.btn-container {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  visibility: hidden;
}

.btn-play {
  background-color: transparent;
}

.icon-play {
  color: #fff;
  font-size: 18px;
  width: 18px;
}
